
.VideoTimeline {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  height: 3px;
}
.VideoTimeline .progress {
  position: absolute;
  height: 3px;
  background-color: #fff;
}
.VideoTimeline input[type=range]::webkit-slider-thumb {
  -webkit-appearance: none;
}
.VideoTimeline input[type=range]:focus {
  outline: none;
  border: none;
}
.VideoTimeline input[type=range] {
  -webkit-appearance: none;
  position: absolute;
  width: 100%;
  height: 3px;
  background: transparent;
  border-color: transparent;
  color: transparent;
  cursor: pointer;
  padding: 0;
}
.VideoTimeline input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 3px;
  background-color: rgba(255,255,255,0.2);
  cursor: pointer;
  border: none;
  color: transparent;
}
.VideoTimeline input[type=range]::-moz-range-track {
  width: 100%;
  height: 3px;
  background-color: rgba(255,255,255,0.2);
  cursor: pointer;
  border: none;
  color: transparent;
}
.VideoTimeline input[type=range]::-ms-track {
  width: 100%;
  height: 3px;
  background-color: rgba(255,255,255,0.2);
  cursor: pointer;
  border: none;
  color: transparent;
}
.VideoTimeline input[type=range]::-ms-tooltip {
  display: none;
}
.VideoTimeline input[type=range]::-ms-fill-lower {
  background: transparent;
}
.VideoTimeline input[type=range]::-ms-fill-upper {
  background: transparent;
}
.VideoTimeline input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 0;
  height: 0;
  background: transparent;
  border: none;
  oultine: none;
}
.VideoTimeline input[type=range]::-moz-range-thumb {
  width: 0;
  height: 0;
  background: transparent;
  border: none;
  oultine: none;
}
.VideoTimeline input[type=range]::-ms-thumb {
  width: 0;
  height: 0;
  background: transparent;
  border: none;
  oultine: none;
}


